@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@400;500;600;700&family=Lato:wght@300;400;700&display=swap);
:root {
  --primary-orange: #e65c00;
  --primary-orange-light: #ff7b1c;
  --primary-orange-dark: #cc5200;
  --yellow: #f5a623;
  --yellow-light: #ffd966;
  --yellow-pale: #fff8e7;
  --cream: #fffdf5;
  --dark-charcoal: #2c2c2c;
  --warm-gray: #6b6b6b;
  --soft-shadow: rgba(230, 92, 0, 0.1);
}

* {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--cream) 0%, var(--yellow-pale) 100%);
  color: var(--dark-charcoal);
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main-container {
  position: relative;
  flex-grow: 1;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Cormorant Garamond', Georgia, serif;
  font-weight: 600;
  color: var(--primary-orange-dark);
}

h3 {
  margin-top: 1rem;
  margin-bottom: 0.25rem;
}

select {
  border: 1px solid var(--yellow);
  padding: 0.5rem 0.75rem;
  background-color: #ffffff;
  border-radius: 4px;
  font-family: 'Lato', sans-serif;
  font-size: 0.9rem;
  color: var(--dark-charcoal);
  cursor: pointer;
  transition: all 0.2s ease;
}

select:hover {
  border-color: var(--primary-orange);
  box-shadow: 0 2px 8px var(--soft-shadow);
}

select:focus {
  outline: none;
  border-color: var(--primary-orange);
  box-shadow: 0 0 0 3px rgba(230, 92, 0, 0.1);
}

.app {
  width: 100%;
  height: 100%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  height: 4rem;
  box-shadow: 0 0.25rem 0.5rem #777777;
  font-size: 1.5rem;
  font-weight: bold;
  padding-left: 1rem;
  z-index: 10;
}

.content-container {
  width: 100%;
  height: 100%;
  max-height: 100%;
  padding-top: 4rem;
}

.content {
  position: absolute;
  width: 100%;
  max-width: 60rem;
  left: 50%;
  max-height: 100%;
  transform: translateX(-50%);
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  overflow-y: scroll;
}

.content::-webkit-scrollbar {
  /* WebKit */
  display: none;
}

.full {
  width: 100%;
  height: 100%;
}

.floating-container {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
}

.floating-button {
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  box-shadow: 0 0.25rem 0.5rem #777777;
  font-size: 1.5rem;
  padding: 0;
  margin: 0;
  z-index: 5;
}

.floating-container-center {
  position: fixed;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
}

.floating-button-large {
  border: none;
  border-radius: 50%;
  width: 4.5rem;
  height: 4.5rem;
  cursor: pointer;
  box-shadow: 0 0.25rem 0.5rem #777777;
  font-size: 2rem;
  padding: 0;
  margin: 0;
}

.freq-note,
.freq-hz {
  display: block;
}
.freq-note {
  font-size: 28px;
  font-family: 'Cormorant Garamond', Georgia, serif;
  font-weight: 600;
  color: var(--primary-orange-dark);
}
.freq-hz {
  font-family: 'Lato', sans-serif;
  color: var(--warm-gray);
  font-size: 14px;
}
.freq-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(245, 166, 35, 0.08) 0%, transparent 70%);
}
.freq-pointer {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}
.freq-note-label {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  font-size: 18px;
  font-family: 'Cormorant Garamond', Georgia, serif;
  font-weight: 500;
  color: var(--primary-orange-dark);
}
.freq-octave {
  font-size: 0.6em;
  vertical-align: sub;
  color: var(--yellow);
}
.freq-surround {
  position: relative;
  overflow: hidden;
  min-width: 300px;
  min-height: 300px;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--cream) 0%, var(--yellow-pale) 100%);
}


.setting > *:last-child {
  display: block;
  text-align: right;
  font-size: smaller;
}
.setting > *:first-child {
  display: block;
  text-align: left;
  font-style: normal;
  font-weight: 500;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--yellow-pale);
}
::-webkit-scrollbar-thumb {
  background: var(--yellow);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-orange);
}

/* Bootstrap overrides for classy look */
.navbar {
  padding: 1rem 1.5rem !important;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary-orange) 0%, var(--primary-orange-light) 100%);
  border: none;
  transition: all 0.3s ease;
}
.btn-primary:hover {
  background: linear-gradient(135deg, var(--primary-orange-light) 0%, var(--primary-orange) 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(230, 92, 0, 0.3);
}

:root {
  --sidenav-closed-width: 60px;
  --sidenav-open-width: 18em;
  --sidenav-bg: linear-gradient(180deg, #e65c00 0%, #cc5200 100%);
  --sidenav-accent: #ffd966;
  --sidenav-text: #fffdf5;
}

.sidenav {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  min-width: 64px;
  height: 100%;
  background: var(--sidenav-bg);
  color: var(--sidenav-text);
  display: flex;
  flex-direction: column;
  min-width: var(--sidenav-closed-width);
  width: var(--sidenav-closed-width);
  transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding-bottom: 0.5em;
  overflow-x: hidden;
  z-index: 2000;
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.15);
}
.sidenav.expanded {
  width: var(--sidenav-open-width);
}

.sidenav-toggle .hamburger {
  transform: scale(0.6);
  margin: -15px;
}
.sidenav-toggle {
  width: var(--sidenav-closed-width);
  margin-top: 0.75em;
  text-align: center;
}
.sidenav-toggle:focus-within {
  outline: 1px dotted var(--sidenav-accent);
}

.sidenav .hamburger .hamburger-inner,
.sidenav .hamburger .hamburger-inner::before,
.sidenav .hamburger .hamburger-inner::after,
.sidenav .hamburger.is-active .hamburger-inner,
.sidenav .hamburger.is-active .hamburger-inner::before,
.sidenav .hamburger.is-active .hamburger-inner::after {
  background-color: var(--sidenav-accent);
}
.sidenav .hamburger:focus {
  outline: none;
}

.sidenav-nav {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 1em;
}

.sidenav-item {
  position: relative;
  margin: 4px 8px;
  padding: 0.5em 0;
  display: flex;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.2s ease;
}
.sidenav-item:hover {
  background-color: rgba(255, 217, 102, 0.2);
}

.sidenav-item.active {
  background: linear-gradient(90deg, rgba(255, 217, 102, 0.35) 0%, rgba(255, 217, 102, 0.1) 100%);
  border-left: 3px solid var(--sidenav-accent);
}
.sidenav-item.active:hover {
  background: linear-gradient(90deg, rgba(255, 217, 102, 0.45) 0%, rgba(255, 217, 102, 0.2) 100%);
}
.sidenav-item:active,
.sidenav-item.active:active {
  background-color: rgba(255, 217, 102, 0.3);
  transform: scale(0.98);
}

.sidenav-icon {
  width: var(--sidenav-closed-width);
  min-width: var(--sidenav-closed-width);
  text-align: center;
  border: none;
  background-color: unset;
  color: var(--sidenav-accent);
  transition: color 0.2s ease;
}
.sidenav-item:hover .sidenav-icon {
  color: #fff8e7;
}
.sidenav-icon:focus-visible {
  outline: 1px dotted var(--sidenav-accent);
}
.sidenav-text {
  opacity: 0;
  transition: opacity 0.3s ease;
  width: calc(var(--sidenav-open-width) - var(--sidenav-closed-width));
  min-width: calc(var(--sidenav-open-width) - var(--sidenav-closed-width));
  font-weight: 400;
  letter-spacing: 0.5px;
}
.expanded .sidenav-text {
  opacity: 1;
}

.sidenav-extra {
  opacity: 0;
  transition: opacity 0.3s ease;
  margin-left: var(--sidenav-closed-width);
  min-width: calc(var(--sidenav-open-width) - var(--sidenav-closed-width));
  font-size: 0.85em;
  display: none;
}
.expanded .sidenav-extra {
  display: unset;
  opacity: 1;
}

.sidenav-extra .list-group-item {
  background-color: rgba(255, 253, 245, 0.1);
  border-color: rgba(255, 217, 102, 0.25);
  padding: 0.6rem 0.8rem;
  color: var(--sidenav-text);
  transition: background-color 0.2s ease;
}
.sidenav-extra .list-group-item:hover {
  background-color: rgba(255, 253, 245, 0.15);
}

.sidenav-extra h1 {
  font-size: 1.5em;
}
.sidenav-extra h2 {
  font-size: 1em;
}
.sidenav-extra h3 {
  font-size: 0.7em;
  font-weight: bold;
}
.sidenav-extra .setting-desc {
  margin-right: 0.5em;
  color: var(--sidenav-accent);
  font-weight: 500;
}

.main-container {
  padding-left: var(--sidenav-closed-width);
  overflow: hidden;
}

main {
  height: 100%;
  overflow: auto;
  background: linear-gradient(135deg, #fffdf5 0%, #fff8e7 100%);
}

